          @import "@/assets/core";
        

@import "@/assets/variables";

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 30px;
  min-height: 300px;
}

.title {
  font-size: 22px;
  margin-bottom: 30px;
  text-align: center;
  max-width: 600px;
}

.qr-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.qr-code {
  width: 250px;
  height: 250px;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;

  // Ensure QR code SVG/Canvas fills container
  :deep(svg),
  :deep(canvas) {
    width: 100% !important;
    height: 100% !important;
    display: block;
  }
}

.timer {
  font-size: 18px;
  margin-top: 10px;
  color: var(--text-secondary);
}

.loader-container,
.message-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px;
}

.processing-text {
  margin-top: 20px;
  font-size: 18px;
}

.back-link-container {
  margin-top: 30px;
  text-align: center;
}

.back-link {
  font-weight: 400;
  line-height: normal;
  font-family: inherit;
}

.subtitle-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: -20px;
  margin-bottom: 30px;
}

.subtitle {
  font-size: 14px;
  color: var(--text-secondary);
  text-align: center;
  max-width: 500px;
  margin: 0 auto;

  :deep(a) {
    color: var(--primary);
  }
}

.tv-link {
  margin-top: 8px;
}

.email-linking {
  margin-top: 30px;
  width: 100%;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
}

.email-linking-text {
  font-size: 14px;
  color: var(--text-secondary);
  margin-bottom: 15px;
  text-align: center;
}

.form__group {
  display: flex;
  width: 100%;
  position: relative;
  margin-bottom: 10px;
}

.g-code {
  &__input {
    background-color: var(--tertiary-bg);
    border: 1px solid var(--tertiary-accent);
    border-radius: 5px 0 0 5px;
    padding: 10px 10px;
    font-size: 14px;
    width: 100%;
    color: var(--text-primary);
    height: 40px;
    outline: none;

    &:focus {
      border-color: var(--primary);
    }
  }

  &__button {
    background-color: var(--primary);
    color: #fff;
    border: none;
    border-radius: 0 5px 5px 0;
    padding: 0 15px;
    font-size: 14px;
    cursor: pointer;
    white-space: nowrap;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.2s ease;

    &_loading {
      position: relative;
      pointer-events: none;

      .g-code__loader {
        display: inline-block;
      }
    }
  }

  &__loader {
    display: none;
    width: 16px;
    height: 16px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    border-top-color: #fff;
    animation: spin 1s linear infinite;
    margin-right: 5px;
  }

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }
}
